<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('ManageReplyTemplate') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">                    
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                            <template v-slot:tgslot-Description="{ data }">
                                <ckeditor :editor="editor" @ready="onReady" v-model=data.fieldInfo.value></ckeditor>
                                <span name="Header" class="invalid-feedback d-block">{{ data.error }}</span> 
                            </template>
                        </dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import DataServices from "@/services/DataService";
export default {
    props:{
        TemplateId:{            
            required:true
        }
    },    
    data() {
        return {
            editor: this.CLASSIC_EDITOR,   
            AddEditTemplateID:0,       
            FormSchema: [
                {
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Canned Reply Name'),
                            name: "CannedReplyName",
                            value: "",
                            placeholder: this.$t('EnterCannedReplyName'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            config: {
                                options: [{ value: "1001", name: "Active" }, { value: "1002", name: "InActive" }]
                            },
                            placeholder: this.$t('SelectStatus'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "RadioField",
                            label: this.$t('Accessibility'),
                            name: "Accessibility",
                            value: "0",
                            config: {
                                options: [{ value: "1", name: "Public" }, { value: "0", name: "Private" }]
                            },
                            placeholder: this.$t('SelectStatus'),
                            validationRules: { "required": true, max: 100 },
                        }                        
                    ]
                },  
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SlotField",
                            label: this.$t('Description'),
                            name: "Description",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],

                            },
                            settings: {
                                isInSlot: true
                            },
                            visibility: true,
                            validationRules: { "required": true, max: 4000 },
                        },
                    ]
                }               
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Submit",
                    onClick: this.UploadFileWithClaim
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close
                }
            ]
        }
    },
    created: async function () {
        if (this.TemplateId > 0) {
            this.FetchData();
       }

    },
    methods: {
        onReady(editor) {
            editor.ui.getEditableElement().parentElement.insertBefore(
                editor.ui.view.toolbar.element,
                editor.ui.getEditableElement()
            );
        },
        FetchData: async function () {
            var vm=this;
            var url=`TemplateID=${vm.TemplateId}`;
            DataServices.GetTemplateDetails(url).then(response=>{                
                if (response.data.status == 'Success') {
                    if (response.data.result.length>0) {                         
                        var GetTemplateData=response.data.result[0];
                        vm.FormSchema[0].Data[0].value=GetTemplateData.TEMPLATE_NAME;
                        vm.FormSchema[0].Data[1].value=GetTemplateData.STATUS_ID;
                        vm.FormSchema[0].Data[2].value=GetTemplateData.IS_PUBLIC;
                        vm.FormSchema[1].Data[0].value=GetTemplateData.TEMPLATE_DESC;
                        vm.AddEditTemplateID=GetTemplateData.TEMPLATE_ID;
                    }
                }
            });
        },             
        Close: function () {
           this.$parent.IsAddCannedReply=false;
        },
        onSubmit(formfields, val) {                        
            var vm = this;            
            var jsonobj = {
                TemplateId:vm.AddEditTemplateID,
                CannedReplyName: formfields.CannedReplyName,
                StatusId: formfields.Status,
                Accessibility:formfields.Accessibility, 
                Description:formfields.Description, 
            };
            var params = JSON.stringify(jsonobj);
            DataServices.SaveCannedReply(params).then((response) => {                
                if (response.data.status == 'Success') {                    
                    if (response.data.result == "INSERT") {                       
                        vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("CannedReply")]), "success", true, vm.$t("Alert"));
                        vm.Close();
                    }
                    else if (response.data.result == 'UPDATE') {
                        vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("CannedReply")]), "success", true, vm.$t("Alert"));                        
                    }
                    else if(response.data.result=="ALLREADYEXIST"){                      
                        vm.ShowAlert(vm.$t("AlreadyExists", [vm.$t("CannedReply")]), "failure", true, vm.$t("Alert"));
                    }else{
                        vm.ShowAlert(vm.$t("AddedError", [vm.$t("Resource")]), "failure", true, vm.$t("Alert"));
                    }
                    vm.Close();
                    vm.$parent.FetchData();
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Resource")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
    }
}
</script>