<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="false" ModuleName="Ticketing" SubModuleCode="CANNED_REPLY"
            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" IdentityColumn="TEMPLATE_ID"
            :HeaderText="$t('CannedReplyTemplate')" :ListData="SAData" :HeaderData="Headers"
            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
            :ListDataCallBackFunction="FetchData" :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy"
            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'STATUS_ID'">
                    <listing-status :isfor="'CannedReply'" v-if="isCRMShowStausDrop(data.row.STATUS_ID, 'Ticketing')"
                        ref="updatestatus" v-bind:dataid="data.row.TEMPLATE_ID" data-controller="Ticketing"
                        v-bind:statuslist="StatusListForListing" v-bind:item="data.STATUS_ID" data-action="index"
                        v-bind:dataselected="data.row.STATUS_ID == 1001 ? 'Active' : 'Inactive'" v-bind:callbackfunction="FetchData"
                        v-bind:isselectedvaluestring="true">
                    </listing-status>                
                </template> 
                <template v-if="data.column.COLUMN_NAME == 'TEMPLATE_NAME'" >                   
                <a  :title="data.row[data.column.COLUMN_NAME]" @click="EditCannedReply(data.row.TEMPLATE_ID)"
                    data-toggle-tooltip="tooltip" class="textstyle">
                    {{ data.row[data.column.COLUMN_NAME] }}
                </a>
            </template>           
            </template>
        </tg-list>
        <AddCannedReply v-if="IsAddCannedReply" :TemplateId="TemplateId"></AddCannedReply>
    </section>
</template>
<script>
import Schema from './ReplytemplateSchema'
import DataServices from "@/services/DataService";
import AddCannedReply from './AddCannedReply.vue'
export default {
    components:{AddCannedReply},
    data() {
        return {
            isLoading: false,
            IsAddCannedReply: false,
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            StatusListForListing: [{ STATUS: 'Active', STATUS_ID: 1001, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#28a745" },
            { STATUS: 'Inactive', STATUS_ID: 1002, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#d72435" }],
            Headers: [{
                "COLUMN_NAME": "TEMPLATE_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Title",
                "DISPLAY_ORDER": 1,
                settings : {
                                isInSlot: true,
                            }

            }, {

                "COLUMN_NAME": "STATUS_ID",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 2,
                settings : {
                                isInSlot: true,
                            }

            }],
            SAData: [],
            SortBy: '',
            SortExp: '',
            TotalRecords: 0,
            PageNumber:1,
            PageSize:10,
            searchCondition:'',
            TemplateId:null
        }
    },
    created: function () {
        this.FetchData();
    },
    methods:{
        FetchData:function(){  
            var vm=this;
            vm.isLoading=true;
            var url = `sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&${vm.searchCondition}`;
            DataServices.GetTemplateList(url).then(response=>{                
                if(response.data.status=='Success'){
                    vm.isLoading=false
                    if(response.data.result.length>0){                        
                        vm.SAData=response.data.result;                        
                        vm.TotalRecords = response.data.result[0].TOTAL_RECORD;
                        vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                    }else{
                        vm.SAData=[];
                    }
                }
            })
        },
        commonHeaderButtonClick: function (event) {            
            switch (event.callbackfunction) {
                case "openaddCannedReply":                    
                    this.openaddCannedReply();
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                case "updateStatus":                    
                    this.UpdateStatus(event.id);
                    break;
            }
        },
        renderActions: function () { },
        //listheaderbuttons: function () { },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        leftCommonSearch: function (value) {               
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchConditionJson != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        actionButtonClick: function () { },
        sortdata: function (sortBy) {            
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchData();
        },
        openaddCannedReply:function(){ 
            this.TemplateId=this.TemplateId;           
            this.IsAddCannedReply=true;
        },
        EditCannedReply:function(ths){
            this.TemplateId=ths;
            this.IsAddCannedReply=true;
        },
        UpdateStatus: async function (status) {            
            var vm = this;
            if (status == 'Active') {
                status = 1001
            }
            else {
                status = 1002
            }
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmUpdateStatusRecord"), true, (vm.$t("Active") + "  " + vm.$t("Inactive")), function () {
                    var url = `id=${val}&statusId=${status}&isFor=${'ReplyTemplate'}`;
                    DataServices.updatestatus(url).then(response => {                        
                        if (response.data == true) {
                            vm.listheaderbuttons[1].isdisabled=true;
                            vm.listheaderbuttons[2].isdisabled=true;
                            vm.listheaderbuttons[3].isdisabled=true;
                            vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Status")]), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        }
                    })
                })
            }
        },
        commonDeleteData: function (event) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Crew")), function () {                    
                    let url = `ids=${val}&tableName=TALYGEN_REPLY_TEMPLATE&primaryKey=REPLY_TEMPLATE_ID`; 
                    DataServices.DeleteData(url).then(response => {                        
                        var box = "";
                        if (response.data){
                            vm.listheaderbuttons[1].isdisabled=true;
                            vm.listheaderbuttons[2].isdisabled=true;
                            vm.listheaderbuttons[3].isdisabled=true;
                            vm.FetchData();
                            box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Template')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Template')]), "failure", true, vm.$t('Alert'));
                        }
                        setTimeout(function () {
                            box.modal('hide');
                        }, 2000);

                    });
                });
            }
            else {
                alert(vm.$t("Selectanyrecordtodelete"));
            }
        }
    }    
}
</script>
<style>
.textstyle{
    color: #007bff;
    text-decoration: none;
    background-color: transparent;
}
</style>